@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');

body, html, #root, .app, .container {
    width: 100%;
    height: 100%;
}

.container {
    font-family: "Roboto Light", sans-serif;
    font-size: 28px;
    color: #292929;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #d4ebf2;
}

.auth-form {
    font-family: 'Roboto', sans-serif;
    box-sizing: border-box;
    width: 100%;
    max-width: 400px;
    padding-left: 20px;
    padding-right: 20px;
}

form.input>input {
    font-family: 'Roboto', sans-serif;
    box-sizing: border-box;
    outline: none;
    border: solid 2px #f2dbd4;
    border-radius: 4px;
    color: #292929;
    width: 100%;
    padding: 12px;
    font-size: 14px;
    background: rgba(255, 255, 255, 1);
    margin-top: 0.5em;
}

#post-response-errors>ul {
    font-size: 12px;
    display: table;
    margin: 0 auto;
    padding-top: 1em;
}

.submit-button {
    padding-top: 8px;
}

.btn {
    width: 100%;
    min-width: 100px;
    color: #565656;
    padding: 12px;
    font-size: 14px;
    font-weight: bold;
    font-family: 'Roboto', sans-serif;
    border: solid 2px #f2dbd4;
    border-radius: 4px;
    background: #f2dbd4;
    cursor: pointer;
}